                    td.text-center 6,8
            p.mb-4 De lo anterior se concluye que el consumo diario para este caso es de 6,8 kWh-d칤a, y asumiendo que se consume lo mismo durante los treinta (30) d칤as del mes, el consumo mensual es de 204 kWh-mes.
