        p.my-4(data-aos="fade-down-right") El m칠todo de la demanda calculada es utilizado normalmente en aplicaciones muy sencillas. En aplicaciones m치s complejas donde la elaboraci칩n de un cuadro de cargas y un cuadro de c치lculo resulta compleja de obtener debido a la gran cantidad de equipos conectados en las instalaciones del usuario interesado en instalar un sistema fotovoltaico, se prefiere utilizar medidores de energ칤a que tengan la capacidad de medir y registrar en una memoria interna los valores de energ칤a el칠ctrica en cada minuto u hora del d칤a. Normalmente estos medidores se instalan en la acometida el칠ctrica de entrada y la instalaci칩n debe ser lo suficientemente segura para garantizar la seguridad de las personas y de los equipos instalados. Existen medidores con diferentes tecnolog칤as desde los m치s avanzados llamados analizadores de red hasta los m치s sencillos medidores monof치sicos.
        p En las siguientes im치genes se muestran algunos medidores y la visualizaci칩n de la curva de consumo energ칠tico que se obtiene como resultado: 
