      h2 3.3 Sombreamiento, inclinaci칩n y orientaci칩n
    p Para complementar la tem치tica se invita a explorar la siguiente did치ctica. 
    p.mb-5(data-aos="fade-down-right") Para el an치lisis de los sistemas fotovoltaicos, existen p칠rdidas de energ칤a asociadas a la instalaci칩n de los paneles y a los obst치culos presentes alrededor de la instalaci칩n. En general existen dos tipos de p칠rdidas asociadas al lugar de la instalaci칩n:
    .row.justify-content-center.fondo-1.py-4
      .col-lg-4.col-md-6.col-sm-7.col-8.mb-4.mb-lg-0(data-aos="fade-down-right")
        img.m-auto(src='@/assets/curso/tema3/figura4.svg')
      .col-lg-8.col-md-12.px-4.px-lg-0(data-aos="fade-down-left")
        h5 P칠rdidas por sombreamiento
        p El c치lculo de los sombreamientos es muy importante para cuantificar las p칠rdidas que se pueden tener por este efecto en una instalaci칩n. La presencia de sombras, tendr치 como consecuencia la disminuci칩n de la producci칩n de electricidad. Por lo tanto, para el dise침o de los sistemas fotovoltaicos se deben implementar configuraciones de instalaci칩n de los paneles solares de tal forma que se evite la influencia de las sombras. 
        p El sombreamiento puede ser causado por: edificios, monta침as, otros paneles, etc. Para identificar los efectos del sombreamiento producidos por los movimientos del sol y los elementos, se utilizan herramientas como los diagramas estereogr치ficos, los cuales permiten calcular si se va a tener sombras en la instalaci칩n, una vez sean conocidos el acimut y la altura del edificio u obst치culo. Normalmente este tipo de p칠rdidas se calculan v칤a <em>software</em> o se utilizan tablas espec칤ficas para cada pa칤s donde se presentan unos factores de p칠rdidas que se deben aplicar.
    .row.justify-content-center.fondo-19.py-4.my-5
      .col-lg-8.col-md-12.px-4.order-last.order-lg-first(data-aos="fade-down-left")
        h5 P칠rdidas por inclinaci칩n y orientaci칩n
        p Para obtener el mayor rendimiento energ칠tico del panel solar, se debe tener en cuenta la inclinaci칩n y la orientaci칩n del panel o del arreglo de paneles solares a instalar y dependiendo del pa칤s y la zona donde se van a instalar los paneles, las condiciones son diferentes. Otro punto a tener en cuenta es que, si por ejemplo se quiere aprovechar el tejado de una vivienda o de una industria para la instalaci칩n, este tejado ya tiene una inclinaci칩n que no se puede cambiar. Por lo tanto, en el momento de dise침ar el sistema fotovoltaico, se tendr치n en cuenta este tipo de detalles para analizar la producci칩n final de electricidad.
        p Para el caso de Colombia, al estar ubicados cerca de la l칤nea ecuatorial y al no existir estaciones como en otros pa칤ses, se tiene una gran ventaja porque las condiciones ambientales son muy parecidas durante todo el a침o. Debido a lo anterior, la orientaci칩n 칩ptima para Colombia es instalar los paneles solares hacia el sur geogr치fico del lugar de instalaci칩n. Adicionalmente, la inclinaci칩n ideal de los paneles solares en Colombia est치 entre 10춿 y 15춿 con respecto a la l칤nea paralela al suelo. Lo anterior para garantizar que con esa misma inclinaci칩n, el panel solar pueda limpiarse de manera f치cil.
        p En la siguiente imagen se puede observar la orientaci칩n de los paneles hacia el sur y el concepto gr치fico del grado de inclinaci칩n.
        p En muchas ocasiones no ser치 posible que la instalaci칩n de los paneles solares cumpla con la orientaci칩n ni la inclinaci칩n m치s adecuada porque normalmente los paneles solares se instalan sobre superficies o tejados ya construidos y existen cantidad de obst치culos que no permiten obtener los par치metros 칩ptimos. 
        p Como consecuencia, la producci칩n de electricidad bajar치 dependiendo de qu칠 tan alejado estaremos de esos valores 칩ptimos. 
        p Aunque es posible calcular manualmente las p칠rdidas por orientaci칩n e inclinaci칩n de los paneles solares en una instalaci칩n, normalmente se recurre a herramientas de simulaci칩n que permiten observar la producci칩n de electricidad variando la posici칩n de los paneles.
 

      .col-lg-4.col-md-6.col-sm-7.col-8.mb-4.mb-lg-0.order-first.order-lg-last(data-aos="fade-down-right")
        img(src='@/assets/curso/tema3/figura5.svg')
        img.mt-4(src='@/assets/curso/tema3/imagen16.jpg')
        p.mt-3 Orientaci칩n de los paneles hacia el sur y el concepto gr치fico del grado de inclinaci칩n.
