      h2 3.4 Ejemplo de dimensionamiento
    p Conocer por medio de la siguiente did치ctica un ejemplo de dimensionamiento.
    p.mb-5(data-aos="fade-down-right") Para el dimensionamiento de las instalaciones solares fotovoltaicas actualmente se utiliza <em>software</em> especializado. Sin embargo, para aplicaciones peque침as como viviendas rurales se utiliza un m칠todo de c치lculo sencillo que permite el dise침o de instalaciones con herramientas convencionales como hojas de c치lculo. En este cap칤tulo se pretende dimensionar de forma b치sica una instalaci칩n solar fotovoltaica de tipo aut칩nomo para la electrificaci칩n de una vivienda rural que quiere autoabastecer su consumo sin energ칤a de la red principal. 
    .row.justify-content-center.mb-5
      .col-lg-6.col-md-12.mb-4.mb-lg-0.fondo-32.px-5.pt-5(data-aos="fade-down-right")
        p Cabe mencionar que se hace como ejercicio did치ctico, por lo tanto, algunos detalles de la instalaci칩n como planos constructivos, diagramas unifilares el칠ctricos, puesta a tierra y dem치s no son tenidos en cuenta. Para los sistemas conectados a red se pueden utilizar algunos de los pasos mostrados a continuaci칩n, sin embargo, al ser un tipo de sistema diferente algunos componentes como las bater칤as no est치n presentes. Actualmente existen varios m칠todos para el dimensionamiento de sistemas aut칩nomos que se pueden elegir, para esta ocasi칩n se muestran los pasos a seguir de acuerdo con Bayod (2019):
      .col-lg-6.col-md-12(data-aos="fade-down-left")
        img(src='@/assets/curso/tema3/imagen9.jpg')
    .row.justify-content-center
      .col-lg-10.col-md-12.mb-3(data-aos="fade-down-right")
        h4 A. Identificaci칩n del lugar de instalaci칩n y condiciones ambientales
      .col-lg-10.col-md-12.fondo-8.px-3(data-aos="fade-down-left")
