      .col-lg-10.col-md-12.mt-5.mb-4(data-aos="fade-down-right")
        h4 C. Determinaci칩n de la energ칤a necesaria para el dise침o
      .col-lg-10.col-md-12.fondo-8.p-4(data-aos="fade-down-left")
        p Una vez se calcula la energ칤a el칠ctrica consumida por la vivienda, es necesario aplicar a este valor factores de multiplicaci칩n para que el dise침o est칠 por encima de las especificaciones m칤nimas requeridas y sean suplidas las p칠rdidas que se presentan en los dem치s elementos del sistema como cables, regulador y conductores entre otros.  El primero de los factores se denomina margen de seguridad y resulta de aumentar en un 15% el valor del consumo calculado en el paso anterior. Teniendo en cuenta lo anterior, el nuevo consumo con el margen de seguridad incluido del 15% es:
        .row.justify-content-center.my-4
          .col-lg-8.col-md-10.col-sm-12.fondo-blanco.redondearpuntas.sombras.mt-4.mt-lg-0
            .row.justify-content-center.align-items-center
              .col-xl-9.col-lg-8.col-md-12.pt-3.order-last.order-lg-first
                p.text-center <em>Consumo Margen 1 = Consumo calculado * (1 + 15%) = 2.644 Wh * (1+0,15) = 3.040,6 Wh = 3,04 kWh-d칤a</em>
              .col-xl-3.col-lg-4.col-md-4.col-sm-4.col-5.order-first.first-lg-last
                img.correrarriba(src='@/assets/curso/tema3/figura7.svg')
        p Una vez calculado el Consumo aplicando el Margen 1, se debe tener en cuenta la eficiencia de conversi칩n de corriente continua a corriente alterna, el cual en ning칰n momento es del 100%. Siempre existen unas p칠rdidas asociadas que dependen de la tecnolog칤a del inversor y de la curva de funcionamiento del mismo. Normalmente la eficiencia de los inversores var칤a entre 80% y 90% o mayor, sin embargo, para este ejercicio se tomar치 una eficiencia del 85%. La f칩rmula utilizada para calcular la demanda de energ칤a el칠ctrica total para dise침o es:
        .row.justify-content-center.my-4
          .col-lg-8.col-md-10.col-sm-12.fondo-blanco.redondearpuntas.sombras.mt-4.mt-lg-0
            .row.justify-content-center.align-items-center
              .col-xl-9.col-lg-8.col-md-12.pt-3.order-last.order-lg-first
                p.text-center <em>Consumo_dise침o = Consumo_(Margen 1) / Eficiencia Inversor % = 3.040,6 洧녥 / (85/100) =3.577,18 Wh</em>
              .col-xl-3.col-lg-4.col-md-4.col-sm-4.col-5.order-first.first-lg-last
                img.correrarriba(src='@/assets/curso/tema3/figura8.svg')
      .col-lg-10.col-md-12.mt-5.mb-4(data-aos="fade-down-right")
        h4 D. Evaluaci칩n de la irradiaci칩n global horizontal diaria disponible
      .col-lg-10.col-md-12.fondo-8.p-4(data-aos="fade-down-left")
        p En este paso se debe dirigir a las fuentes de informaci칩n disponible de tal forma que se pueda encontrar el valor de la irradiaci칩n global horizontal disponible en la zona de instalaci칩n del sistema fotovoltaico. Para este caso en particular, se utiliza el Mapa nacional multianual de irradiaci칩n global horizontal medio diario anual. (p치gina 74 del documento IDEAM, UPME. (2017)) en donde se determina que el valor de la irradiaci칩n global para la ciudad de Bucaramanga se encuentra en un rango entre 3,5 y 4 (kWh )/m2. Para garantizar que el dise침o est칠 por encima de las especificaciones, se toma el valor menor del rango. Es decir, para el presente ejercicio se tomar치 el valor de 3,5  (kWh )/m2 = 3500 (Wh )/m2 Una vez se determina este valor, se procede a calcular las horas solares pico (HSP) de la siguiente manera:
        .row.justify-content-center.my-4
          .col-lg-8.col-md-10.col-sm-12.fondo-blanco.redondearpuntas.sombras.mt-4.mt-lg-0
            .row.justify-content-center.align-items-center
              .col-12.pt-3
                p.text-center.mb-0 HSP =  (Irradiaci칩n global horizontal diaria)/(1000 W/m2)=3500/1000=3,5 horas
                p.text-center Es elecci칩n del dise침ador escoger la fuente de informaci칩n. Se recomienda para dise침os reales, hacer uso de las bases de datos para obtener valores m치s confiables.        
    //- titulo con figura
    .row.align-items-center.mb-0.mt-5(data-aos="fade-down-right")
      .col-sm-1.col-2
        img.flecha(src='@/assets/curso/tema1/triangulo.svg')
