        h4.mb-0.ps-0.ms-0 C치lculo del n칰mero de paneles solares y la potencia pico a instalar
    p.mb-5(data-aos="fade-down-right") Para el c치lculo de la cantidad de paneles solares y la potencia pico a instalar, se debe seleccionar primero el panel solar a utilizar que se encuentre disponible en el mercado y que se adapte a las condiciones requeridas por el lugar de instalaci칩n. Existen varias potencias nominales que se pueden seleccionar. Para este caso en particular, se seleccion칩 el panel solar con Potencia pico de 300 W. La f칩rmula para calcular la cantidad total de paneles solares (Np) requeridos es la siguiente:
    .row.justify-content-center
      .col-lg-10.col-md-12.p-4.fondo-33(data-aos="fade-down-right")
        p.text-center.mb-0 Np = 1,1*(R/I) / Potencia del panel elegido =1,1 * (1.022,05 W) / 300 W = 3,74 paneles 
        p.text-center La constante de 1,1 tiene relaci칩n a un margen de seguridad de potencia que se sugiere para el c치lculo.
        p.text-center Como el valor del Np no es un n칰mero entero, se aproxima al valor pr칩ximo entero que para este caso es 4 paneles.
        p.text-center.mb-0 El valor de la potencia pico del sistema completo se calcula multiplicando el n칰mero de paneles por la potencia pico de cada panel.
        p.text-center Potencia_(pico del sistema)=Np * Potencia individual del panel seleccionado = 4*300W = 1200 W_pico
    //- titulo con figura
    .row.align-items-center.mb-0.mt-5(data-aos="fade-down-right")
      .col-sm-1.col-2
        img.flecha(src='@/assets/curso/tema1/triangulo.svg')
      .col-sm-11.col-10.correr-izq
        h4.mb-0.ps-0.ms-0 Dise침o del sistema de acumulaci칩n
    p.my-4(data-aos="fade-down-right") El sistema de acumulaci칩n es una de las partes m치s importantes para el dise침o, ya que se compone de bater칤as que soportan el consumo de energ칤a en horas de la noche y madrugada donde no existe o la radiaci칩n solar es muy baja. Para evaluar el tama침o del sistema de acumulaci칩n, es necesario definir para el dise침o las siguientes variables:
    .tarjeta.fondo-1.p-4.mb-5(data-aos="fade-down-right")
      SlyderA.my-5(tipo="b")
        .row.justify-content-center
          .col-lg-6.col-md-12.mb-4.mb-lg-0
            h3 D칤as de autonom칤a (D):
            p Corresponde al tiempo en d칤as que debe funcionar la instalaci칩n fotovoltaica sin recibir radiaci칩n solar en las condiciones de dise침o. Este valor se determina teniendo en cuenta las condiciones de la instalaci칩n como por ejemplo si tiene una planta de energ칤a de respaldo, o si es muy cr칤tico el uso de la energ칤a para esa vivienda o locaci칩n. 
        
          .col-lg-5.col-md-12
            figure
              img(src='@/assets/curso/tema3/imagen9.png')
        .row.justify-content-center
          .col-lg-6.col-md-12.mb-4.mb-lg-0
            h3 M치xima profundidad de descarga (Mpd):
            p Corresponde al l칤mite de descarga que puede alcanzar la bater칤a. Para aplicaciones rurales se recomienda tomar un valor de 70%.
          .col-lg-5.col-md-12
            figure
              img(src='@/assets/curso/tema3/imagen10.png')

        .row.justify-content-center
          .col-lg-6.col-md-12.mb-4.mb-lg-0
            h3 Voltaje de trabajo de la instalaci칩n (Vdc): 
            p Corresponde al voltaje en DC al que trabajar치n los componentes como paneles solares, regulador de carga, entrada del inversor y bater칤as. Este voltaje puede ser generalmente de 12,24 o 48 voltios. Se debe tener en cuenta que el voltaje de trabajo debe ser el mismo para todos los elementos seleccionados. Tambi칠n se debe tener en cuenta que, para instalaciones fotovoltaicas con altas potencias como los parques solares, los voltajes pueden llegar hasta 600 VDC.

          .col-lg-5.col-md-12
            figure
              img(src='@/assets/curso/tema3/imagen11.png')

        .row.justify-content-center
          .col-lg-6.col-md-12.mb-4.mb-lg-0
            h3 Consumo de dise침o (C_dise침o): 
            p.mb-0 Corresponde al valor de dise침o del consumo de electricidad calculado en el numeral c.
            p.mb-0 La capacidad de acumulaci칩n (Q) tiene unidades de Amperio-hora (Ah) y se calcula con la siguiente f칩rmula:
            p.mb-0 <em>Q = (110*C_dise침o*D)/(V_dc*M_pd)</em>
            p.mb-0 Para el caso del ejemplo se consideran los siguientes valores:
            p.mb-0 <em>D = 2 洧녬칤洧녩洧 , M_pd=70% , V_dc=12 Voltios DC, C_dise침o=3.577,18 Wh-d칤a</em>
            p.mb-0 <em>Q = (110*3.577,18*2)/(12*70)=936,88 Ah</em>
        
          .col-lg-5.col-md-12
            figure
              img(src='@/assets/curso/tema3/imagen12.png')

        .row.justify-content-center
          .col-lg-6.col-md-12.mb-4.mb-lg-0
            h3 Ahora para seleccionar el n칰mero de bater칤as:
            p Se debe buscar en el mercado en lo posible bater칤as de 12 voltios (o arreglos de bater칤as en serie y paralelo de 2 voltios) que tengan la mayor capacidad para disminuir su cantidad ya que son elementos costosos. Para este ejercicio, se seleccionan bater칤as GEL de 12 V y 250 Ah de capacidad cada una. Como las bater칤as tienen el mismo voltaje del sistema de 12V, se pueden conectar en paralelo y la cantidad de bater칤as se define como:
            p N칰mero de bater칤as =Q /capacidad unitaria de bater칤a  = 936,88/250 = 3,74 bater칤as. 
            p Al igual que con los paneles solares, la cantidad de bater칤as final se aproxima al n칰mero entero mayor, que en nuestro caso es 4 unidades.
       
          .col-lg-5.col-md-12
            figure
